.plus {
  display: flex;
  margin: 0 -10px;
  padding-top: 60px;

  &__item {
    width: 25%;
    padding: 0 10px;
  }

}

.plus-card {
  height: 100%;
  padding: 16px 12px;
  border-radius: 4px;
  border: solid 1px #d6d6d6;

  &__img-box {
    display: flex;
    align-items: center;
    width: 50px;
    height: 50px;
  }

  &__img {

    &_1 {

    }

    &_2 {

    }

    &_3 {

    }

    &_4 {

    }

  }

  &__title {
    margin: 17px 0 0 0;
    font-family: $font-family-two;
    font-size: 16px;
    font-weight: bold;
    font-style: normal;
    font-stretch: expanded;
    line-height: normal;
    letter-spacing: -0.6px;
    color: #000000;
  }

  &__description {
    margin: 17px 0 0 0;
    font-family: $font-family-one;
    font-size: 12px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 0.3px;
    color: #000000;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 1024px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .plus {
    flex-wrap: wrap;
    margin: 0 -10px;
    padding-top: 60px;

    &__item {
      width: 50%;
      padding: 0 10px;
      margin-bottom: 31px;

      &:nth-child(3) {
        margin-bottom: 0;
      }

      &:nth-child(4) {
        margin-bottom: 0;
      }

    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .plus {
    flex-wrap: wrap;
    margin: 0;
    padding-top: 35px;

    &__item {
      width: 100%;
      padding: 0;
      margin-bottom: 16px;

      &:nth-child(3) {
        margin-bottom: 16px;
      }

      &:nth-child(4) {
        margin-bottom: 0;
      }

    }

  }

}

