.banner {
  position: relative;
  display: flex;
  padding-bottom: 130px;

  &:before {
    content: '';
    position: absolute;
    top: 44px;
    left: 0;
    width: 400px;
    height: 600px;
    background: url(
    data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAFCAYAAACNbyblAAAAGklEQVQYV2O8efPmf3V1dUYGJIDCgYlTKAgAmdsEBm+q+KgAAAAASUVORK5CYII=
    ) repeat;
  }

  //d8d8d8

  &:after {
    content: '';
    position: absolute;
    z-index: 1;
    top: 56px;
    left: -150px;
    width: 837px;
    height: 344px;
    background: url("../images/banner-1.svg") no-repeat;
    background-size: 837px 344px;
    transition: left ease-in-out .2s;
  }

  #{$body-class} & {

    &:before {
      opacity: 0.3;
    }

    &:after {
      content: '';
      position: absolute;
      top: 56px;
      left: -150px;
      width: 837px;
      height: 344px;
      background: url("../images/banner-2.svg") no-repeat;
      background-size: 837px 344px;
      transition: left ease-in-out .2s;
    }

  }

}

.banner-content {
  position: relative;
  margin-left: auto;
  max-width: 515px;
  padding-top: 150px;

  & h1 {
    margin: 0;
    font-family: $font-family-two;
    font-size: 44px;
    font-weight: bold;
    font-style: normal;
    font-stretch: expanded;
    line-height: 1.09;
    letter-spacing: -1.1px;
    color: #000000;
  }

  & p {
    margin: 20px 0 0 0;
    font-family: $font-family-one;
    font-size: 16px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.5;
    letter-spacing: normal;
    color: #000000;
  }

  #{$body-class} & {

    & h1 {
      color: #fff;
    }

    & p {
      color: #fff;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1300px) {

  .banner {

    &:before {
      left: -15px;
    }

    &:after {
      left: -300px;
    }

    #{$body-class} & {

      &:before {
        left: -15px;
      }

      &:after {
        left: -300px;
      }

    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1150px) {

  .banner {

    &:after {
      left: -400px;
    }

    #{$body-class} & {

      &:after {
        left: -400px;
      }

    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1024px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .banner {
    flex-direction: column;
    padding-bottom: 54px;
    border-bottom: solid 1px #d6d6d6;

    &:after {
      top: 87px;
      left: -170px;
    }

    #{$body-class} & {

      &:after {
        top: 87px;
        left: -170px;
      }

    }

  }

  .banner-content {
    margin-left: 0;
    max-width: 515px;
    padding-top: 454px;

    & h1 {
      margin: 0;
      font-family: $font-family-two;
      font-size: 52px;
      font-weight: bold;
      font-style: normal;
      font-stretch: expanded;
      line-height: 1.08;
      letter-spacing: -1.3px;
      color: #000000;
    }

    & p {
      margin: 20px 0 0 0;
      font-family: $font-family-one;
      font-size: 16px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.5;
      letter-spacing: normal;
      color: #000000;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .banner {
    padding-bottom: 45px;

    &:before {
      content: none;
    }

    &:after {
      top: 63px;
      left: -50px;
      width: 332px;
      height: 174px;
      background-size: 332px 174px;
    }

    #{$body-class} & {

      &:before {
        content: none;
      }

      &:after {
        top: 63px;
        left: -50px;
        width: 332px;
        height: 174px;
        background-size: 332px 174px;
      }

    }

  }

  .banner-content {
    margin-left: 0;
    max-width: 515px;
    padding-top: 271px;

    & h1 {
      margin: 0;
      font-family: $font-family-two;
      font-size: 30px;
      font-weight: bold;
      font-style: normal;
      font-stretch: expanded;
      line-height: 1.33;
      letter-spacing: -0.8px;
      color: #000000;
    }

    & p {
      margin: 15px 0 0 0;
      font-family: $font-family-one;
      font-size: 16px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.5;
      letter-spacing: normal;
      color: #000000;
    }

  }

}

