.customer {

  &_ml {
    margin-left: 60px;
  }

  &_mt {
    margin-top: 120px;
  }

  &__title {
    margin: 0;
    font-family: $font-family-one;
    font-size: 16px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 1px;
    color: #808080;
    text-transform: uppercase;
  }

}

.customer-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -30px;

  &__item {
    width: 33.33%;
    padding: 15px 30px;
  }

  &__img-box {
    padding: 10px;
    border-radius: 4px;
    background-color: rgba(216, 216, 216, 0.17);
  }

  &__img {
    width: 100%;
    vertical-align: top;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1024px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .customer {

    &_ml {
      margin-left: 0;
    }

    &_mt {
      margin-top: 40px;
    }

  }

  .customer-list {
    margin: 0 -15px;

    &__item {
      padding: 15px 15px;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .customer-list {
    margin: 0 -15px;

    &__item {
      width: 50%;
      padding: 15px 15px;
    }

    &__img-box {

    }

    &__img {

    }

  }

}

