.team-section {
  margin-top: 85px;
  border-top: 1px solid #3b3b3b;

  &__title {
    margin: 40px 0 0 0;
    font-family: $font-family-two;
    font-size: 40px;
    font-weight: bold;
    font-style: normal;
    font-stretch: expanded;
    line-height: normal;
    letter-spacing: -1px;
    color: #fff;
  }

}

.team {
  display: flex;
  flex-wrap: wrap;
  margin-top: 10px;

  &__item {
    margin-top: 50px;
    width: 33.33%;
  }

  &__img-box {
    width: 170px;
    height: 170px;
  }

  &__img {
    width: 100%;
    vertical-align: top;
  }

  &__name {
    margin: 28px 0 0 0;
    font-family: $font-family-two;
    font-size: 16px;
    font-weight: bold;
    font-style: normal;
    font-stretch: expanded;
    line-height: normal;
    letter-spacing: -0.6px;
    color: #ffffff;
  }

  &__text {
    max-width: 80%;
    margin: 13px 0 0 0;
    font-family: $font-family-one;
    font-size: 16px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.5;
    letter-spacing: normal;
    color: #ffffff;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1024px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .team-section {
    margin-top: 50px;
  }

  .team {

    &__item {
      width: 50%;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .team-section {
    margin-top: 45px;

    &__title {
      margin: 35px 0 0 0;
      font-size: 30px;
      font-weight: bold;
      font-style: normal;
      font-stretch: expanded;
      line-height: normal;
      letter-spacing: -0.8px;
      color: #ffffff;
    }

  }

  .team {
    display: flex;
    flex-wrap: wrap;
    margin-top: 10px;

    &__item {
      margin-top: 40px;
      width: 100%;
    }

    &__text {
      max-width: 100%;
    }

  }

}

