.contact-form {
  display: flex;
  align-items: center;

  &_wt {

  }

  &__inp {
    padding: 10px;
    min-width: 71px;
    font-family: $font-family-one;
    font-size: 16px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #888888;
    background: transparent;
    border-radius: 4px 0 0 4px;
    border: solid 1px #000000;
    outline: none;
    transition: opacity ease-in-out .2s;

    &:hover {
      opacity: .8;
    }

    &_wt {
      border: solid 1px #888888;
    }

  }

  &__btn {
    margin-left: -1px;
    padding: 10px;
    min-width: 170px;
    font-family: $font-family-one;
    font-size: 16px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #ffffff;
    border-radius: 0 4px 4px 0;
    border: solid 1px #000000;
    background: #000;
    outline: none;
    cursor: pointer;
    transition: opacity ease-in-out .2s;

    &:hover {
      opacity: .8;
    }

    &_wt {
      color: #000;
      background-color: #ceff23;
      border: solid 1px #ceff23;
    }

  }

}

.contact-form-wrap {
  display: none;
  align-items: center;
  padding: 20px 25px;
  border-radius: 4px;
  border: solid 1px #000000;
  outline: 1px solid #ceff23;
  outline-offset: -3px;

  &_mt {
    margin-top: 81px;
  }

  &__col {
    width: 50%;
  }

  &__text {
    font-family: $font-family-one;
    font-size: 12px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.33;
    letter-spacing: normal;
    color: #000000;

    &_wt {
      color: #888;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1024px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .contact-form {

    &_hide {
      display: none;
    }

  }

  .contact-form-wrap {
    display: flex;

    &_wt {
      border: solid 1px #ceff23;
      outline: none;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .contact-form {

    &__btn {
      padding: 12px 10px;
      min-width: 120px;
      font-size: 12px;
    }

  }

  .contact-form-wrap {
    flex-wrap: wrap;
    flex-direction: column-reverse;
    padding: 12px 17px;

    &_mt {
      margin-top: 40px;
    }

    &__col {
      width: 100%;
    }

    &__text {
      margin-top: 13px;
      font-family: $font-family-one;
      font-size: 12px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.33;
      letter-spacing: normal;
      color: #000000;

      &_wt {
        color: #888;
      }

    }

  }

}

