@font-face {
  font-family: 'Lato', sans-serif;
  src: url("../fonts/lato/Lato-Regular.eot"),
  url("../fonts/lato/Lato-Regular.woff2") format("woff2"),
  url("../fonts/lato/Lato-Regular.woff") format("woff"),
  url("../fonts/lato/Lato-Regular.ttf") format("truetype"),
  url("../fonts/lato/Lato-Regular.svg") format("svg");

  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Lato', sans-serif;
  src: url("../fonts/lato/Lato-Bold.eot"),
  url("../fonts/lato/Lato-Bold.woff2") format("woff2"),
  url("../fonts/lato/Lato-Bold.woff") format("woff"),
  url("../fonts/lato/Lato-Bold.ttf") format("truetype"),
  url("../fonts/lato/Lato-Bold.svg") format("svg");

  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'RFDewi';
  src: url("../fonts/dewi/RF-Dewi-Expanded-Bold.eot"),
  url("../fonts/dewi/RF-Dewi-Expanded-Bold.woff") format("woff"),
  url("../fonts/dewi/RF-Dewi-Expanded-Bold.ttf") format("truetype"),
  url("../fonts/dewi/RF-Dewi-Expanded-Bold.svg") format("svg");

  font-weight: bold;
  font-style: normal;
}
