.footer {
  padding: 70px 0 150px;
  background-color: #202020;

  #{$body-class} & {
    background-color: #fff;
  }


}

.footer-title {
  margin: 0;
  font-family: $font-family-two;
  font-size: 32px;
  font-weight: bold;
  font-style: normal;
  font-stretch: expanded;
  line-height: normal;
  letter-spacing: 0.9px;
  color: #ffffff;

  #{$body-class} & {
    color: #000;
  }

}

.footer-title-sm {
  margin: 15px 0 0;
  font-family: $font-family-one;
  font-size: 16px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #ffffff;

  #{$body-class} & {
    color: #000;
  }

}

.footer-menu {
  margin: 33px 0 0;
  padding: 0;
  list-style: none;

  & li {
    margin-bottom: 15px;

    &:last-child {
      margin-bottom: 0;
    }


    & a {
      font-family: $font-family-one;
      font-size: 24px;
      font-weight: 800;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: -0.8px;
      color: #aaaaaa;
      text-decoration: none;
      transition: opacity ease-in-out .2s;

      &:hover {
        opacity: .8;
      }

    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 1024px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .footer {
    padding: 70px 0 58px;
  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .footer {
    padding: 30px 0;
  }

  .footer-title-sm {
    margin: 42px 0 0;
  }

}

