.review {
  margin-bottom: 55px;
  background: #202020;

  &__description {
    margin: 0;
    font-family: $font-family-one;
    font-size: 16px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.5;
    letter-spacing: normal;
    color: #ffffff;
  }

  &__label {
    padding-left: 30px;
    font-family: $font-family-one;
    font-size: 16px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 1px;
    color: #aaaaaa;
    text-transform: uppercase;
  }

}

.review-body {
  display: flex;
  padding: 30px 25px 25px;
}

.review-footer {
  padding: 0 25px 25px;
}

.review-user {
  display: flex;

  &__img-box {
    width: 48px;
    height: 48px;
    border-radius: 2px;
    overflow: hidden;
  }

  &__img {
    width: 100%;
  }

  &__detail-box {
    padding-left: 12px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__name {
    font-family: $font-family-one;
    font-size: 16px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #ffffff;
  }

  &__text {
    padding-top: 5px;
    font-family: $font-family-one;
    font-size: 12px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #aaaaaa;

    & a {
      color: #f25f3a;
      transition: opacity ease-in-out .2s;

      &:hover {
        opacity: .8;
      }

    }

  }

}

