/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .review {
    margin-bottom: 40px;

    &__description {
      order: 2;
      margin: 18px 0 0;
      font-family: $font-family-one;
      font-size: 16px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.5;
      letter-spacing: normal;
      color: #ffffff;
    }

    &__label {
      order: 1;
      padding-left: 0;
      font-family: $font-family-one;
      font-size: 12px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: 0.8px;
      color: #aaaaaa;
    }

  }

  .review-body {
    flex-direction: column;
    padding: 20px 10px 20px;
  }

  .review-footer {
    padding: 0 10px 20px;
  }

  .review-user {

    &__img-box {
      width: 48px;
      height: 48px;
      border-radius: 2px;
      overflow: hidden;
    }

    &__img {
      width: 100%;
    }

    &__detail-box {
      padding-left: 12px;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    &__name {
      font-family: $font-family-one;
      font-size: 16px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: normal;
      color: #ffffff;
    }

    &__text {
      padding-top: 5px;
      font-family: $font-family-one;
      font-size: 12px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: normal;
      color: #aaaaaa;

    }

  }

}

