.btn {
  display: inline-block;
  padding: 11px;
  font-family: $font-family-one;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #000000;
  text-decoration: none;
  border-radius: 4px;
  background-color: #ceff23;
  border: 0;
  outline: none;
  cursor: pointer;
  transition: opacity ease-in-out .2s;

  &:hover {
    opacity: .8;
  }

}

.btn-link-arrow {
  position: relative;
  display: inline-block;
  padding-right: 30px;
  padding-bottom: 5px;
  font-family: $font-family-two;
  font-size: 24px;
  font-weight: bold;
  font-style: normal;
  font-stretch: expanded;
  line-height: 1;
  letter-spacing: normal;
  color: #000000;
  text-decoration: none;
  border-bottom: 3px solid #000;
  cursor: pointer;
  transition: opacity ease-in-out .2s;

  &:hover {
    opacity: .8;
  }

  &:after {
    content: '';
    position: absolute;
    top: 8px;
    right: 0;
    width: 21px;
    height: 12px;
    background-image: url(../images/link-arrow.svg);
    background-repeat: no-repeat;
    background-size: 21px 12px;
  }

  &_mt {
    margin-top: 110px;
  }

  &_ml {
    margin-left: 60px;
  }

  &_footer {
    margin-top: 167px;
    color: #fff;
    border-bottom: 3px solid #fff;

    &:after {
      background-image: url(../images/link-arrow_wt.svg);
    }

  }

  #{$body-class} & {
    color: #fff;
    border-bottom: 3px solid #fff;

    &:after {
      background-image: url(../images/link-arrow_wt.svg);
    }

    &_footer {
      color: #000;
      border-bottom: 3px solid #000;

      &:after {
        background-image: url(../images/link-arrow.svg);
      }

    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1024px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .btn-link-arrow {

    &:after {
      top: 5px;
    }

    &_mt {
      margin-top: 22px;
    }

    &_ml {
      margin-left: 0;
    }

    &_footer {
      margin-top: 67px;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .btn-link-arrow {
    font-size: 18px;

    &_mt {
      margin-top: 22px;
    }

    &_ml {
      margin-left: 0;
    }

    &_footer {
      margin-top: 67px;
    }

  }

}

