.content {
  position: relative;

  &_pt {
    padding-top: 50px;
  }

  & h2 {
    margin: 0 0 21px 0;
    font-family: $font-family-two;
    font-size: 40px;
    font-weight: bold;
    font-style: normal;
    font-stretch: expanded;
    line-height: normal;
    letter-spacing: -1px;
    color: #000000;
  }

  & p {
    margin: 0 0 21px 0;
    font-family: $font-family-one;
    font-size: 16px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.5;
    letter-spacing: normal;
    color: #000000;

    & a {
      color: #000000;
      transition: opacity ease-in-out .2s;

      &:hover {
        opacity: .8;
      }
    }

  }

  #{$body-class} & {

    & h2 {
      color: #fff;
    }

    & p {
      color: #fff;

      & a {
        color: #fff;
      }

    }

  }


}

