/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .content {

    &_pt {
      padding-top: 30px;
    }

    & h2 {
      margin: 0 0 14px 0;
      font-family: $font-family-two;
      font-size: 30px;
      font-weight: bold;
      font-style: normal;
      font-stretch: expanded;
      line-height: normal;
      letter-spacing: -0.8px;
      color: #000000;
    }

    & p {
      margin: 0 0 21px 0;
      font-family: $font-family-one;
      font-size: 16px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.5;
      letter-spacing: normal;
      color: #000000;

      & a {
        color: #000000;
        transition: opacity ease-in-out .2s;

        &:hover {
          opacity: .8;
        }
      }

    }

  }

}

