.header {

}

.header-wrap {
  padding-top: 23px;
  display: flex;
  align-items: center;
}

.header-nav {
  display: flex;
  align-items: center;
  margin: 0 0 0 auto;
  padding: 0;
  list-style: none;

  &__item {
    margin-left: 50px;

    &:first-child {
      margin-left: 0;
    }

  }

  &__link {
    font-family: $font-family-one;
    font-size: 16px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #000000;
    text-decoration: none;
    transition: opacity ease-in-out .2s;

    &:hover {
      opacity: .8;
    }

    &_active {
      border-bottom: 1px solid #000;
    }

    #{$body-class} & {
      color: #fff;

      &_active {
        border-bottom: 1px solid #fff;
      }

    }

  }

}

.header-menu-btn {
  display: none;
  position: relative;
  padding: 0;
  width: 26px;
  background: transparent;
  border: 0;
  outline: none;
  transition: opacity ease-in-out .2s;

  &:hover {
    opacity: .8;
  }

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: #000;
  }

  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: #000;
  }

  & span {
    display: block;
    margin: 8px 0;
    width: 26px;
    height: 2px;
    background: #000;
  }

  #{$body-class} & {
    &:before {
      height: 2px;
      background: #fff;
    }

    &:after {
      background: #fff;
    }

    & span {
      background: #fff;
    }
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 1024px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .header-nav {

    &__item {

      &_hide {
        display: none;
      }

    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .header-wrap {
    padding-top: 23px;
    justify-content: space-between;
  }

  .header-nav {
    display: none;
    position: absolute;
    z-index: 99;
    right: 15px;
    top: 80px;

    &__item {
      margin-left: 20px;
    }

    &.toggled {
      display: flex;
    }

  }

  .header-menu-btn {
    display: inline-block;
  }

}

