.prg-section {
  margin-top: 85px;
  border-top: 1px solid #d6d6d6;

  &__title {
    margin: 40px 0 0 0;
    font-family: $font-family-two;
    font-size: 40px;
    font-weight: bold;
    font-style: normal;
    font-stretch: expanded;
    line-height: normal;
    letter-spacing: -1px;
    color: #000000;
  }

}

.prg-row {
  display: flex;
  margin: 0 -30px;

  &_mt {
    margin-top: 65px;
  }

}

.prg-col {
  width: 50%;
  padding: 0 30px;
}

.prg-card {
  padding: 30px 25px 0;
  margin-bottom: 55px;

  &_bg-1 {
    background: #fafafa;
  }

  &_bg-2 {
    background-image: linear-gradient(to bottom, #deefff, #eff4ff);
  }

  &_bg-3 {
    background-image: linear-gradient(133deg, rgba(255, 142, 100, 0.2), rgba(255, 230, 65, 0.2));
  }

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__title {
    margin: 0;
    font-family: $font-family-two;
    font-size: 16px;
    font-weight: bold;
    font-style: normal;
    font-stretch: expanded;
    line-height: normal;
    letter-spacing: -0.6px;
    color: #000000;
  }

  &__name {
    margin: 0;
    font-family: $font-family-one;
    font-size: 16px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 1px;
    color: #93aecc;
    text-transform: uppercase;
  }

  &__description {
    max-width: 80%;
    margin: 15px 0 0 0;
    font-family: $font-family-one;
    font-size: 16px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.5;
    letter-spacing: normal;
  }

  &__img-box {
    margin-top: 50px;
  }

  &__img {
    width: 100%;
    vertical-align: top;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1024px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .prg-section {
    margin-top: 73px;

    &__title {
      margin: 47px 0 0 0;
      font-size: 40px;
    }

  }

  .prg-row {
    display: flex;
    flex-wrap: wrap;
    margin: 0;

    &_mt {
      margin-top: 47px;
    }

  }

  .prg-col {
    width: 100%;
    padding: 0;
  }

  .prg-card {
    margin-bottom: 40px;
  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .prg-section {
    margin-top: 40px;

    &__title {
      margin: 32px 0 0 0;
      font-size: 30px;
    }

  }

  .prg-row {

    &_mt {
      margin-top: 20px;
    }

  }

  .prg-card {
    padding: 20px 10px 0;
    margin-bottom: 40px;

    &__header {
      flex-direction: column;
      align-items: flex-start;
      justify-content: left;
    }

    &__title {
      order: 1;
      margin: 16px 0 0;
      font-family: $font-family-two;
      font-size: 16px;
      font-weight: bold;
      font-style: normal;
      font-stretch: expanded;
      line-height: normal;
      letter-spacing: -0.6px;
      color: #000000;
    }

    &__name {
      margin: 0;
      font-family: $font-family-one;
      font-size: 12px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: 0.8px;
      color: #cecdc6;
    }

    &__description {
      max-width: 100%;
      margin: 15px 0 0 0;
      font-family: $font-family-one;
      font-size: 16px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.5;
      letter-spacing: normal;
      color: #000000;
    }

    &__img-box {
      margin-top: 24px;
    }

    &__img {
      width: 100%;
      vertical-align: top;
    }

  }

}

